nav 
    width: 18%
    height: 100vh 
    position: fixed
    padding-top: 12vh
    padding-left: 4vh
    z-index: 8
    

    a
        display: block 
        margin: 1vh 0
        color: var(--Primary)
        font-weight: 500
        font-size: 4vh
        pointer-events: all

    .menu_option
        pointer-events: none

        .dropdown
            overflow: hidden
            height: 0
            transition: .5s

            a li 
                margin: 1vh 0
                font-size: 3vh
            a, a li
                pointer-events: all

        .open
            color: var(--Third)
            
        .selected
            color: var(--Secondary )